<template>
  <ion-page>
    <ion-header mode="ios">
      <ion-toolbar>
        <ion-title>Informasi Pengajuan Izin</ion-title>
        <ion-back-button slot="start" default-href="/" />
      </ion-toolbar>
    </ion-header>
    <ion-content :fullscreen="true">
      <ion-refresher slot="fixed" @ionRefresh="doRefresh($event)">
        <ion-refresher-content />
      </ion-refresher>
      <ion-content class="ion-padding">
        <ion-item>
          <ion-label>Search by Name</ion-label>
          <ion-input
            v-model="searchQuery"
            placeholder="Enter name to search"
          ></ion-input>
        </ion-item>
        <ion-item>
          <ion-label>Bulan</ion-label>
          <ion-select v-model="selectedMonth" placeholder="Pilih Bulan">
            <ion-select-option
              v-for="month in months"
              :key="month.value"
              :value="month.value"
              >{{ month.label }}</ion-select-option
            >
          </ion-select>
        </ion-item>
        <ion-button style="color: white" expand="block" @click="filterIzin"
          >Search</ion-button
        >
        <ion-grid>
          <ion-row>
            <ion-col>
              <h2>Daftar Pengajuan Izin</h2>
              <ion-list>
                <ion-item v-for="izin in filteredIzin" :key="izin.id">
                  <ion-label>
                    <h3>{{ izin.fullName }} - {{ izin.role }}</h3>
                    <p>Status: {{ izin.izinStatus }}</p>
                    <p>Durasi: {{ izin.durasi }} hari</p>
                    <p>Keterangan: {{ izin.keterangan }}</p>
                    <p>Dari: {{ formatDate(izin.startDate) }}</p>
                    <p>Sampai: {{ formatDate(izin.endDate) }}</p>
                    <p>Status Admin: {{ izin.status }}</p>
                    <p>Keterangan Admin: {{ izin.keteranganStatus }}</p>
                  </ion-label>
                  <ion-button
                    color="success"
                    @click="openModal(izin.id, 'disetujui')"
                    >Setujui</ion-button
                  >
                  <ion-button
                    color="danger"
                    @click="openModal(izin.id, 'ditolak')"
                    >Tolak</ion-button
                  >
                </ion-item>
              </ion-list>
              <ion-button expand="block" @click="loadMore" v-if="showLoadMore"
                >Load More</ion-button
              >
            </ion-col>
          </ion-row>
        </ion-grid>
        <ion-button
          style="color: white"
          expand="block"
          @click="exportFilteredToExcel"
          >Export Laporan Sakit</ion-button
        >
      </ion-content>

      <ion-toast
        :is-open="toast.isOpen"
        :message="toast.message"
        :duration="toast.duration"
        :color="toast.color"
        @did-dismiss="() => (toast.isOpen = false)"
      ></ion-toast>

      <ion-modal :is-open="isModalOpen" @did-dismiss="closeModal">
        <ion-header>
          <ion-toolbar>
            <ion-title>Ubah Status Izin</ion-title>
            <ion-buttons slot="end">
              <ion-button @click="closeModal">Close</ion-button>
            </ion-buttons>
          </ion-toolbar>
        </ion-header>
        <ion-content>
          <ion-item>
            <ion-label>Keterangan</ion-label>
            <ion-textarea
              v-model="keteranganStatus"
              placeholder="Masukkan keterangan"
            ></ion-textarea>
          </ion-item>
          <ion-button expand="block" @click="ubahStatus">Simpan</ion-button>
        </ion-content>
      </ion-modal>
    </ion-content>
  </ion-page>
</template>
<script setup>
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonBackButton,
  IonContent,
  IonRefresher,
  IonRefresherContent,
  IonItem,
  IonLabel,
  IonSelect,
  IonSelectOption,
  IonInput,
  IonTextarea,
  IonButton,
  IonList,
  IonToast,
  IonCol,
  IonRow,
  IonGrid,
  IonModal,
  IonButtons,
} from "@ionic/vue";
import { ref, onMounted, computed } from "vue";
import { getAuth, onAuthStateChanged } from "firebase/auth";
import {
  getFirestore,
  doc,
  updateDoc,
  getDocs,
  collection,
  query,
  where,
  limit,
  startAfter,
} from "firebase/firestore";
import { firebaseApp } from "../firebase.js";
import * as XLSX from "xlsx";

const auth = getAuth(firebaseApp);
const db = getFirestore(firebaseApp);

const daftarIzin = ref([]);
const toast = ref({
  isOpen: false,
  message: "",
  duration: 2000,
  color: "success",
});
const isModalOpen = ref(false);
const keteranganStatus = ref("");
const selectedIzinId = ref("");
const selectedStatus = ref("");
const searchQuery = ref("");
const izinPerPage = ref(10);
const lastVisible = ref(null);
const showLoadMore = ref(false);
const selectedMonth = ref("");
const months = [
  { value: 0, label: "January" },
  { value: 1, label: "February" },
  { value: 2, label: "March" },
  { value: 3, label: "April" },
  { value: 4, label: "May" },
  { value: 5, label: "June" },
  { value: 6, label: "July" },
  { value: 7, label: "August" },
  { value: 8, label: "September" },
  { value: 9, label: "October" },
  { value: 10, label: "November" },
  { value: 11, label: "December" },
];

const filteredIzin = computed(() => {
  return daftarIzin.value
    .filter((izin) => {
      const fullNameMatches = izin.fullName
        .toLowerCase()
        .includes(searchQuery.value.toLowerCase());
      const izinMonth = izin.startDate
        ? izin.startDate.toDate().getMonth()
        : null;
      const monthMatches =
        selectedMonth.value === "" || izinMonth === selectedMonth.value;

      return fullNameMatches && monthMatches;
    })
    .slice(0, izinPerPage.value);
});

const fetchIzinData = async (reset = false) => {
  let q;
  if (reset) {
    q = query(
      collection(db, "izin"),
      where("fullName", ">=", searchQuery.value),
      where("fullName", "<=", searchQuery.value + "\uf8ff"),
      limit(izinPerPage.value)
    );
  } else {
    q = query(
      collection(db, "izin"),
      where("fullName", ">=", searchQuery.value),
      where("fullName", "<=", searchQuery.value + "\uf8ff"),
      startAfter(lastVisible.value),
      limit(izinPerPage.value)
    );
  }

  try {
    const querySnapshot = await getDocs(q);
    const newIzin = querySnapshot.docs.map((doc) => ({
      id: doc.id,
      ...doc.data(),
      startDate: doc.data().startDate,
      endDate: doc.data().endDate,
    }));

    if (reset) {
      daftarIzin.value = newIzin;
    } else {
      daftarIzin.value.push(...newIzin);
    }

    lastVisible.value = querySnapshot.docs[querySnapshot.docs.length - 1];
    showLoadMore.value = querySnapshot.docs.length === izinPerPage.value;

    console.log("Data izin berhasil diambil:", daftarIzin.value);
  } catch (error) {
    console.error("Error fetching izin data:", error);
    showToast("Terjadi kesalahan saat mengambil data izin", "danger");
  }
};

const filterIzin = () => {
  fetchIzinData(true);
};

const loadMore = () => {
  fetchIzinData();
};

const openModal = (izinId, status) => {
  selectedIzinId.value = izinId;
  selectedStatus.value = status;
  isModalOpen.value = true;
};

const closeModal = () => {
  isModalOpen.value = false;
  keteranganStatus.value = "";
  selectedIzinId.value = "";
  selectedStatus.value = "";
};

const ubahStatus = async () => {
  try {
    await updateDoc(doc(db, "izin", selectedIzinId.value), {
      status: selectedStatus.value,
      keteranganStatus: keteranganStatus.value,
    });
    showToast(
      `Status pengajuan izin berhasil diubah menjadi ${selectedStatus.value}`,
      "success"
    );
    fetchIzinData(true); // Refresh the izin data after updating status
    closeModal(); // Close the modal after saving
  } catch (error) {
    console.error("Error updating izin status:", error);
    showToast("Terjadi kesalahan saat mengubah status izin", "danger");
  }
};

const doRefresh = (event) => {
  setTimeout(() => {
    fetchIzinData(true);
    event.target.complete();
  }, 2000);
};

const showToast = (message, color) => {
  toast.value = {
    isOpen: true,
    message: message,
    duration: 2000,
    color: color,
  };
};

const formatDate = (timestamp) => {
  if (!timestamp) return "N/A";
  const date = timestamp.toDate();
  const day = date.getDate().toString().padStart(2, "0");
  const month = (date.getMonth() + 1).toString().padStart(2, "0");
  const year = date.getFullYear();
  return `${day}/${month}/${year}`;
};

const exportFilteredToExcel = () => {
  const izinData = filteredIzin.value.filter(
    (izin) => izin.izinStatus === "sakit" || izin.izinStatus === "izin"
  );

  if (izinData.length === 0) {
    showToast(
      "Tidak ada data izin atau sakit untuk bulan yang dipilih",
      "warning"
    );
    return;
  }

  const exportData = izinData.map((izin) => ({
    fullName: izin.fullName,
    role: izin.role,
    izinStatus: izin.izinStatus,
    durasi: izin.durasi,
    keterangan: izin.keterangan,
    fromDate: formatDate(izin.startDate),
    toDate: formatDate(izin.endDate),
    status: izin.status,
    keteranganStatus: izin.keteranganStatus,
  }));

  const worksheet = XLSX.utils.json_to_sheet(exportData);
  const workbook = XLSX.utils.book_new();
  XLSX.utils.book_append_sheet(workbook, worksheet, "Laporan Izin dan Sakit");
  XLSX.writeFile(workbook, "laporan_izin_dan_sakit.xlsx");
  showToast("Laporan izin dan sakit berhasil diekspor", "success");
};

onMounted(() => {
  onAuthStateChanged(auth, (user) => {
    if (user) {
      fetchIzinData(true);
    } else {
      console.error("User not authenticated");
      showToast("Pengguna tidak terautentikasi", "danger");
    }
  });
});
</script>

<style>
/* Custom styling if needed */
</style>
